<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <search-book @search-book="searchBookToReview" />
  <div class="list-books">
    <book-item v-for="book in books" :key="book.id" :bookProps="book" />
  </div>
</template>

<script>
import { ref } from 'vue'

import axios from 'axios'

import BookItem from './BookItem.vue'
import SearchBook from './SearchBook.vue'

export default {
  name: 'contentGgBook',
  components: { BookItem, SearchBook },
  setup() {
    var books = ref([])

    const searchBookToReview = async value => {
      try {
        const res = await axios.get(
          `https://www.googleapis.com/books/v1/volumes?q=${value}&key=AIzaSyBzcS0Hn5iURn8mTN12pCFbC8uYOKSjIbU`
        )
        const data = res.data.items
        books.value.length = 0

        for (let i = 0; i < res.data.items.length; i++) {
          let newBook = {}
          newBook.id = data[i].id
          newBook.title = data[i].volumeInfo.title
          newBook.subtitle = data[i].volumeInfo.subtitle
          if (data[i].volumeInfo.imageLinks === undefined)
            newBook.thumbnail =
              'https://s3.amazonaws.com/ArchiveImages/LegacyReviews/SLJ/defaultcover.jpg'
          else newBook.thumbnail = data[i].volumeInfo.imageLinks.thumbnail

          books.value.push(newBook)
        }
      } catch (err) {
        console.log(err)
      }
    }

    return {
      books,
      searchBookToReview
    }
  }
}
</script>

<style>
.list-books {
  margin-top: 20px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: stretch;
  align-items: center;
  gap: 10px 10px;
}
</style>
