<template>
  <div class="search-container">
    <input
      type="text"
      placeholder="Book name ..."
      v-model="searchValue"
      @keyup.enter="searchBook"
    />
    <button class="search-btn" @click="searchBook">Search</button>
  </div>
</template>

<script>
import { ref } from 'vue'
import slug from 'slug'

export default {
  name: 'SearchBook',
  setup(props, context) {
    const searchValue = ref('')

    const searchBook = event => {
      event.preventDefault()

      context.emit('search-book', slug(searchValue.value), '#')
    }

    return {
      searchValue,
      searchBook
    }
  }
}
</script>

<style scoped>
.search-container {
  display: flex;
}

input[type='text'] {
  flex: 10;
  padding: 5px;
}

input[type='submit'] {
  flex: 2;
}
</style>
